import { useState } from 'react'
import NavBar from "../NavBar/NavBar"
import Header from "../Header/Header"
import ItemContainer from "../ItemContainer/ItemContainer"
import Footer from "./Footer/Footer"
import "./App.scss"


const App = () => {

    const [sueldo, setSueldo] = useState(27216)

    const [money, setMoney] = useState(sueldo)

    const [gastado, setGastado] = useState(0)


    const onAdd = (precio) => {
        setMoney(money - precio)
        setGastado(gastado + precio)
    }

    const cambiarSueldo = (tipoDeSueldo) => {

        if (tipoDeSueldo === "minimo") {
            setSueldo(27216)
            setGastado(0)
            setMoney(1000)
        } else if (tipoDeSueldo === "promedio") {
            setSueldo(2000)
            setGastado(0)
            setMoney(2000)
        }
    }

    return (
        <>
            <NavBar money={money} />
            <Header money={money} gastado={gastado} sueldo={sueldo} cambiarSueldo={cambiarSueldo} />
            <ItemContainer onAdd={onAdd} />
            <Footer />
        </>
    )
}

export default App