import Container from "react-bootstrap/Container"
import Row from "react-bootstrap/Row"
import Col from "react-bootstrap/Col"
import formatMoney from "../index"
import "./Header.scss"


const Header = ({ money, gastado, sueldo, cambiarSueldo }) => {

    let date = new Date();
    let dateString = date.getDate() + "-" + (date.getMonth() + 1) + "-" + date.getFullYear();

    let moneyClass = "high"

    let sueldos = parseFloat(gastado / sueldo).toFixed(2)

    if (money < 0) {
        moneyClass = "low"
    }

    return (
        <Container className="coverContainer mt-4">
            <Row>
                <Col className="d-flex flex-column py-2 align-items-center text-center">
                    <h1 className="fs-2 mb-3 pt-2">Gastá un sueldo en Argentina</h1>
                    <h2 className={moneyClass}>{formatMoney(money)}</h2>
                    <p>Necesitas <strong>{sueldos}</strong> sueldos para comprar los productos seleccionados.</p>
                    <p>*Salario Mínimo, Vital y Móvil al {dateString}: <strong>{formatMoney(sueldo)}</strong></p>                 
                </Col>
            </Row>
        </Container>
    )

}

/*
 
*/

export default Header